body {
    margin: 0;
    padding: 0;
}

#root {
    width: 100%;
    height: 100vh;
}

.app--container {
    height: 75vh;
    display: flex;
    flex-direction: column;
}

.project--name {
    padding-left: 2rem;
    color: azure;
    text-decoration: none;
    font-weight: bold;
    font-size: 2em;
}

.project--name:hover {
    color:rgb(253, 160, 202);
    transition: 0.6s;
}

/* nav section begins here */
.nav {
    display: flex;
    position: fixed;
    width: 100%;
    height: 50px;
    background-color: rgb(51, 61, 61);
    color: #fff;
    align-items: center;
    justify-content: space-between;
    z-index: 999;
}

.featured--algos {
    display: block;
}

.featured--algos h3 {
    line-height: 50px;
}

.featured--algos:hover {
    .algo--list {
        position: absolute;
        display: block;
        margin-top: 0;
        background-color: rgb(51, 61, 61);
        list-style: none;
        padding-left: 0;
        border-radius: 5px;
    }

    .algo--list li button {
        color: azure;
        width: 200px;
        height: 50px;
        background-color: transparent;
        border: 0;
        border-radius: 5px;
    }

    .algo--list li button:hover {
        color: rgb(51, 61, 61);
        background-color: rgb(253, 160, 202);
        transition: 0.6s;
    }
}

.algo--list {
    display: none;
}

.wall--patterns {
    display: block;
}

.wall--patterns:hover {
    .patterns--list {
        position: absolute;
        display: block;
        margin-top: 0;
        padding-left: 0;
        background-color: rgb(51, 61, 61);
        list-style: none;
        border-radius: 5px;
    }

    .patterns--list li button {
        color: azure;
        width: 200px;
        height: 50px;
        background-color: transparent;
        border: 0;
        border-radius: 5px;
    }

    .patterns--list li button:hover {
        color: rgb(51, 61, 61);
        background-color: rgb(253, 160, 202);
        transition: 0.6s;
    }
}

.wall--patterns h3 {
    line-height: 50px;
}

.patterns--list {
    display: none;
}

.speed--settings {
    display: block;
    padding-right: 2rem;
}

.speed--settings:hover {
    .speeds--list {
        position: absolute;
        display: block;
        margin-top: 0;
        padding-left: 0;
        list-style: none;
        background-color: rgb(51, 61, 61);
        border-radius: 5px;
    }

    .speeds--list li button {
        color: azure;
        width: 100px;
        height: 25px;
        background-color: transparent;
        border: 0;
        border-radius: 5px;
    }

    .speeds--list li button:hover {
        color: rgb(51, 61, 61);
        background-color: rgb(253, 160, 202);
        transition: 0.6s;
    }
}

.speed--settings h3 {
    line-height: 50px;
}

.speeds--list {
    display: none;
}
/* nav section ends here */

/* description section begins here */
.description {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 85%;
    height: 25vh;
    margin: 0 auto;
    list-style: none;
}

.description--element {
    display: inline-flex;
    align-items: center;
    gap: 0.75em;
}

.display--element div {
    display: flex;
    align-items: center;
}
/* decription section ends here */

/* canvas section begins here */
.canvas {
    position: relative;
    width: 95%;
    margin: 0 auto;
}

.node {
    position: relative;
    width: 25px;
    height: 25px;
    border: 0.5px solid rgb(156, 238, 231);
    cursor: pointer;
}

.start {
    width: 25px;
    height: 25px;
    background-image: url(../public/start.png);
    background-size: cover;
    background-repeat: no-repeat;
}

.end {
    width: 25px;
    height: 25px;
    background-image: url(../public/end.png);
    background-size: cover;
    background-repeat: no-repeat;
}

.visited {
    background-color: rgb(253, 160, 202);
}

.filled--node {
    width: 0px;
    height: 0px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    animation: addLayer 0.75s ease-in 0s forwards;
    z-index: -1;
}

@keyframes addLayer {
    from {
        width: 0px;
        height: 0px;
        border-radius: 20px;
        background-color: rgb(139, 13, 70);
    }
    to {
        width: 100%;
        height: 100%;
        border-radius: 0;
        background-color: rgb(253, 160, 202);
     }
}

.path {
    background-color: rgb(252, 255, 155);
    transition: 0.6s;
}

.wall {
    border: 1px solid;
    background-color: black;
    transition: 0.6s;
}

.line {
    display: flex;
}
/* canvas section ends here */

.test {
    pointer-events: none;
    cursor: not-allowed;
    opacity: 0.65;
    filter: alpha(opacity=65);
    -webkit-box-shadow: none;
    box-shadow: none;
}